<!--index -->
<script>
    import TableCommon from "@/components/TableCommon.vue";
    export default {
        extends: TableCommon, //TableCommon
        name: 'index-parent',
        data() {
            return {
                doType: '',
                formObj: {},
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '90px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                rule: [],
                currEditRow: '', //
            }
        },
        methods: {
            handleClose() {
                this.dialogShow = false;
            },
            //
            _findRule(type) {
                return this.rule.find(r => r.field === type);
            },
            // 
            notice() {
                this.dialogShow = false;
                this.loading = false; //
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success',
                    duration: 3000,
                });
                this.reloadTable();
            },
        },
    }
</script>