<template>
    <el-card class="box-card">
        <keep-alive>
            <el-tabs v-model="activeName" @tab-click="tabClick">
                <el-tab-pane label="" name="0">
                    <kcList ref="0"></kcList>
                </el-tab-pane>
                <el-tab-pane label="" name="1">
                    <kpList ref="1"></kpList>
                </el-tab-pane>
            </el-tabs>
        </keep-alive>
    </el-card>
</template>

<script>
    import kcList from "./kcList/index.vue";
    import kpList from "./kpList/index.vue";

    export default {
        name: 'index',
        components: {
            kcList,
            kpList,
        },
        data() {
            return {
                activeName: '0',
            }
        },
        methods: {
            tabClick({
                index
            }) {
                this.$refs[index] && this.$refs[index].reloadTable();
            }
        }
    }
</script>

<style scoped lang="scss">
    .box-card {
        width: 100%;

        /deep/ .el-card__body {
            padding: 0 0.24rem 0.24rem 0.24rem;
        }
    }
</style>