<!---->
<template>
    <div>
        <SearchInputs @searchHand="searchHand" @add="addHand"></SearchInputs>
        <Table :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" @btnClick="btnClick" :pageSize="page.pageSize" :total="total">
        </Table>
        <!---->
        <el-dialog :title="doType" custom-class="cusDialog" :visible.sync="this.dialogShow" width="5rem"
            :before-close="handleClose">
            <div class='model-form'>
                <form-create v-model="formObj" :rule="rule" :option="options" @kc-code-select="selectKc">
                </form-create>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit"> </el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //
    import BoxTitle from "@/components/BoxTitle.vue";
    import Index from "../components/Index.vue";
    import Table from "./Table.vue";
    import SearchInputs from "./SearchInputs.vue";
    //
    import {
        getKcListSearch,
        getBasicKpList,
        postEditBasicKp,
        postAddBasicKp,
        exportMeterRead,
    } from "@/api/archives.js";
    //
    import tool from "@/utils/tool.js";
    import {
        province_city_area
    } from "@/utils/province_city_area.js";
    export default {
        extends: Index, //index
        name: 'kcList',
        components: {
            BoxTitle,
            SearchInputs,
            Table
        },
        data() {
            return {
                rule: [{
                    type: 'input',
                    field: "name",
                    title: '',
                    value: '',
                    props: {
                        placeholder: '',
                    },
                    validate: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                }, {
                    type: 'input',
                    field: "region",
                    title: '',
                    value: '',
                    props: {
                        placeholder: '',
                    },
                    validate: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kcCode',
                    title: '',
                    props: {
                        placeholder: '',
                        fieldValue: 'code',
                        fieldLabel: 'fullName',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false, //
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKcListSearch(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.fullName,
                                    }))
                                    return list;
                                } else {
                                    return [];
                                }
                            })
                        }
                    },
                    validate: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    emit: ['select'],
                }, {
                    type: "cascader",
                    title: "",
                    field: "address",
                    value: ['', '', ''],
                    props: {
                        options: province_city_area
                    },
                    on: {
                        change: (item) => {
                            //  
                            this.formObj.validateField('address', () => {})
                        }
                    },
                    validate: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                }, {
                    type: 'input',
                    field: "kwNum",
                    title: '',
                    value: '',
                    props: {
                        placeholder: '',
                    },
                    validate: [{
                        validator: tool.validator_isNumber,
                        required: true,
                        trigger: 'blur'
                    }],
                }, {
                    type: 'checkbox',
                    field: "wkTy",
                    title: '',
                    value: [],
                    options: [{
                            value: "",
                            label: "",
                        },
                        {
                            value: "",
                            label: "",
                        },
                        {
                            value: "",
                            label: "",
                        },
                        {
                            value: "",
                            label: "",
                        },
                        {
                            value: "",
                            label: "",
                        }
                    ],
                    validate: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                }, {
                    type: 'input',
                    field: "stageNum",
                    title: '',
                    value: '',
                    props: {
                        placeholder: '',
                        min: 0,
                    },
                    validate: [{
                        validator: tool.validator_isNumber,
                        required: true,
                        trigger: 'blur'
                    }],
                }, {
                    type: "radio",
                    title: "",
                    field: "prc",
                    value: "",
                    options: [{
                            value: "",
                            label: "",
                        },
                        {
                            value: "",
                            label: "",
                        }, {
                            value: "",
                            label: "",
                        },
                    ],
                    validate: [{
                        required: true,

                        trigger: 'blur'
                    }],
                }],
            }
        },
        methods: {
            //
            btnClick(btnName, row) {
                this.currEditRow = row; // 
                this.dialogShow = true;
                this.doType = '';
                this.$nextTick(() => {
                    const data = this.formObj.form
                    for (let key in row) {
                        data[key] = row[key];
                    }
                    data.wkTy = row.wkTy ? row.wkTy.split('/') : [];
                    data.address = row.prv ? [row.prv, row.cty, row.cnty] : [];
                    this.formObj.exec('kcCode', 'setLabel', row.kcName)
                })
            },
            //
            addHand() {
                this.currEditRow = 'new'; // 
                this.doType = '';
                this.dialogShow = true;
                this.$nextTick(() => {
                    const data = this.formObj.form
                    this.formObj.resetFields();
                    this.formObj.exec('kcCode', 'setLabel', '')
                })
            },
            //
            _getData(params = {}) { //    
                getBasicKpList({
                    ...this.search,
                    ...this.page
                }).then(res => {
                    const data = res.rows ? res.rows : [];
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            //
            submit() {
                this.formObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.formObj.formData();
                        //todo 
                        this.loading = true;
                        if (this.currEditRow !== 'new') { //
                            postEditBasicKp({
                                ...this.currEditRow,
                                ...data,
                                wkTy: data.wkTy.join('/'),
                                prv: data.address[0],
                                cty: data.address[1],
                                cnty: data.address[2],
                            }).then(this.notice).catch(err => {
                                this.loading = false; //
                                return false
                            })
                        } else { //
                            postAddBasicKp({
                                ...data,
                                wkTy: data.wkTy.join('/'),
                                prv: data.address[0],
                                cty: data.address[1],
                                cnty: data.address[2],
                            }).then(this.notice).catch(err => {
                                this.loading = false; //
                                return false
                            })
                        }
                    }
                })
            },
            //
            selectKc() {
                this.formObj.validateField('kcCode', () => {})
            },
        }
    }
</script>