<!---->
<template>
    <div>
        <SearchInputs @searchHand="searchHand" @add="addHand"></SearchInputs>
        <Table :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" @btnClick="btnClick" :pageSize="page.pageSize" :total="total">
        </Table>
        <!---->
        <el-dialog :title="doType" custom-class="cusDialog" :visible.sync="this.dialogShow" width="5rem"
            :before-close="handleClose">
            <div class='model-form'>
                <form-create v-model="formObj" :rule="rule" :option="options">
                </form-create>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit"> </el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //
    import BoxTitle from "@/components/BoxTitle.vue";
    import Index from "../components/Index.vue";
    import Table from "./Table.vue";
    import SearchInputs from "./SearchInputs.vue";
    //
    import {
        getBasicKcList,
        postEditBasicKc,
        postAddBasicKc,
        exportMeterRead,
    } from "@/api/archives.js";
    //
    export default {
        extends: Index, //index
        name: 'kcList',
        components: {
            BoxTitle,
            SearchInputs,
            Table
        },
        data() {
            return {
                rule: [{
                    type: 'input',
                    field: "fullName",
                    title: '',
                    value: '',
                    props: {
                        placeholder: '',
                    },
                    validate: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                }, {
                    type: 'input',
                    field: "shortName",
                    title: '',
                    value: '',
                    props: {
                        placeholder: '',
                    },
                    validate: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                }],
            }
        },
        methods: {
            //
            btnClick(btnName, row) {
                this.currEditRow = row; // 
                this.dialogShow = true;
                this.doType = '';
                this.$nextTick(() => {
                    const data = this.formObj.form
                    data.fullName = row.fullName;
                    data.shortName = row.shortName;
                })
            },
            //
            addHand() {
                this.currEditRow = 'new'; // 
                this.doType = '';
                this.dialogShow = true;
                this.$nextTick(() => {
                    const data = this.formObj.form
                    this.formObj.resetFields();
                })
            },
            //
            _getData(params = {}) { //    
                getBasicKcList({
                    ...this.search,
                    ...this.page
                }).then(res => {
                    const data = res.rows ? res.rows : [];
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            //
            submit() {
                this.formObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.formObj.formData();
                        //todo 
                        this.loading = true;
                        if (this.currEditRow !== 'new') { //
                            postEditBasicKc({
                                ...this.currEditRow,
                                ...data
                            }).then(this.notice).catch(err => {
                                this.loading = false; //
                                return false
                            })
                        } else { //
                            postAddBasicKc({
                                ...data
                            }).then(this.notice).catch(err => {
                                this.loading = false; //
                                return false
                            })
                        }
                    }
                })
            }
        }
    }
</script>