<!--CusTabledoc-->
<!--
  src\views\archives\calcPoint\overview.vue  
<CusTable :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></CusTable>

:
tableData: {Array} data  
@handleSizeChange: {String} 
@handleCurrentChange: {String} 
-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table row-key="index" :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
            :row-class-name="tableRowClassName">
            <el-table-column label="" type="index" width="90" />
            <el-table-column label="" prop="name" width="160"></el-table-column>
            <el-table-column label="" prop="wkTy" width="200"></el-table-column>
            <el-table-column label="" prop="kcName" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="prv" width="300" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row['prv']+'/'+ scope.row['cty']+'/'+scope.row['cnty'] }}</span>
                </template>
            </el-table-column>

            <el-table-column label="" prop="kwNum" width="100"></el-table-column>
            <el-table-column label="" prop="stageNum" width="100"></el-table-column>
            <el-table-column label="" prop="prc" width="90">
                <template slot-scope="scope">
                    <span :class="getClass(scope.row.prc)">{{scope.row['prc']}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <div class="center">
                        <span class="cus-btn" @click="handClick('',scope.row)" type="text" size="small">
                        </span>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>

</template>

<script>
    import Table from "../components/Table.vue";
    export default {
        extends: Table
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        cursor: pointer;
        display: flex;
        padding: 0 10px;
        justify-content: space-between;
        align-items: center;

        .edit-btn:hover {
            color: #2E4AF7
        }

        .delete-btn:hover {
            color: #E6A23C
        }

        .center {
            width: 100%;
        }

    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>